.value-title{
    margin:auto;
}

.container .jumbotron{
    margin-left:0px;
}

.bg-own{
    background-color: #970f1b;
}

.bg-own a, .bg-own {
    color: #fff;
}

.card-body {
    color: #000;
}

.text-own{
    color: #970f1b;
}

.border-own{
    border-color: #970f1b;
}

.border-own .list-group-item{
    border-color: #970f1b;
}

.react-datepicker-wrapper, .react-datepicker__input-container {
    display: block;
  }

.react-datepicker__triangle{
    display:none;
}

.underline {
    text-decoration: underline;
}

.bg-lightwarn, .bg-lightwarn input{
    background:#feffc3 !important;
    color: #000;
}

.div-alt:nth-of-type(odd), .tr-alt:nth-of-type(odd) {
    background-color:#fff;
}

 
.div-alt:nth-of-type(even), .tr-alt:nth-of-type(even) {
    background-color:#f4f4f4;
}

.bg-lightgreen{
    background-color:#d8fad8;
}

.bg-lightred{
    background-color:#f8d4d3;
}
 
table thead.bg-own tr th{
    outline: 1px solid #9b4950;
    border:none;
    outline-offset: -1px;
  }

  table, td:first-child, tr, tbody {
    border-left-color: #970f1b!important;
}

table, td:last-child, tr, tbody {
    border-right-color: #970f1b!important;
} 


table, tr:last-child td,  tbody {
    border-bottom-color: #970f1b!important;
} 